import React from "react";

function Header(props) {
    return (
        <div className="header">
            <div className="header__container">
                <div className="logo"/>
                <ul className="menu">
                    <li className="menu__item is-active"><a href="#product">Product</a></li>
                    <li className="menu__item"><a href="#how">How it works</a></li>
                    <li className="menu__item"><a href="#features">Features</a></li>
                    <li className="menu__item"><a href="#contact">Contact</a></li>
                </ul>
                {props.button}
            </div>
        </div>
    )
}

export default Header;
